<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process247"></a>

<p><br></p>
<p><span class="rvts17">瘦黄的男人（</span><span class="rvts50">The Sallow man</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:259,Y:806</span><span class="rvts21">，地点：黑环营地山洞</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到瘦黄男人，与他交谈，通过语句测试避免战斗，并把亚历山大的头颅献给它。</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1370.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">首先，需要杀掉亚历山大，拿着他的头颅，来到黑环营地的山洞中，使用头颅祭奠祭坛，随后隐藏的石门被打开。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1371.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">与瘦黄男人谈话，尽可能通过语句测试，并交给他亚历山大的头颅。注意，千万不要让洛许与他谈话，那样的话洛许心中的恶魔会非常生气，并最终导致与瘦黄男人的战斗。</span>
<br><span class="rvts21">瘦黄男人会奖励你一把可以</span><span class="rvts20"></span><span class="rvts21">破尸</span><span class="rvts20"></span><span class="rvts21">的法杖，该法杖可以破坏死去的尸体</span><span class="rvts20"> </span><span class="rvts21">，使它永远无法复活。（</span><span class="rvts20"></span><span class="rvts21">薄情寡义</span><span class="rvts20"></span><span class="rvts21">支线的任务物品）</span>
<br><span class="rvts21">另外，还可以向瘦黄男人要一件赏赐，选择</span><span class="rvts20"></span><span class="rvts21">寻求赏赐（</span><span class="rvts20">Claim your reward</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">语句，会让他帮你改变你的标签。然而这样做，将会导致你的某些原有标签失效。（比如说，原来你是贵族，但你想成为野蛮人，结果贵族标签就没了。）</span>
<br><span class="rvts21">具体语句的效果如下：</span>
<br><span class="rvts20">1. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">更加的生性野蛮一些（</span><span class="rvts20">More barbarity in your nature</span><span class="rvts21">）</span><span class="rvts20">?</span><span class="rvts21">，将会得到</span><span class="rvts20"></span><span class="rvts21">野蛮人</span><span class="rvts20"></span><span class="rvts21">标签；</span>
<br><span class="rvts20">2. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">更加幽默一些（</span><span class="rvts20">a better sense of humor</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，将会得到</span><span class="rvts20"></span><span class="rvts21">小丑</span><span class="rvts20"></span><span class="rvts21">标签；</span>
<br><span class="rvts20">3. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">内心渴望逍遥法外（</span><span class="rvts20">inner yearning for an outlaw spirit</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，将会获得</span><span class="rvts20"></span><span class="rvts21">法外狂徒</span><span class="rvts20"></span><span class="rvts21">标签；</span>
<br><span class="rvts20">4. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">总能让自己感到知识渊博（</span><span class="rvts20">always fancied yourself as something of a scholar</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，将获得</span><span class="rvts20"></span><span class="rvts21">学者</span><span class="rvts20"></span><span class="rvts21">标签；</span>
<br><span class="rvts20">5. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">你想成为一名士兵（</span><span class="rvts20">you wanted to be a soldier</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，将获得</span><span class="rvts20"></span><span class="rvts21">士兵</span><span class="rvts20"></span><span class="rvts21">标签；</span>
<br><span class="rvts20">6. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">懂得神秘世界的知识（</span><span class="rvts20">knowledge of the mystical world</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，将获得</span><span class="rvts20"></span><span class="rvts21">神秘</span><span class="rvts20"></span><span class="rvts21">标签；</span>
<br><span class="rvts20">7. </span><span class="rvts21">选择</span><span class="rvts20"></span><span class="rvts21">认识到自己天生就是贵族的命（</span><span class="rvts20">recognized for your innate nobility</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，获得</span><span class="rvts20"></span><span class="rvts21">贵族</span><span class="rvts20"></span><span class="rvts21">标签。</span>
<br><span class="rvts21">在得到瘦黄男人的所有赏识后，可以选择杀掉他，获得额外经验。另外，瘦黄男人会告诉你一些祭坛的日月标示。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1372.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">在瘦黄男人的居所，可以找到一个华丽的宝箱，这里面有一封矮人女王的信。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1373.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">这里还有制作</span><span class="rvts20"></span><span class="rvts21">弃誓者</span><span class="rvts20"></span><span class="rvts21">的一个部件。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1374.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">之后，我们还会在瘦黄男人的居所深处，找到温德戈并打败她。然而她不会给予我们任何的奖励和经验，她会在下一章中的阿克斯与我们再次见面。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



